import React from "react";
import MailchimpSubscribe from "react-mailchimp-subscribe";

function Newsletter({ opacity, to }) {
  const post = process.env.REACT_APP_POST;
  const u = process.env.REACT_APP_U;
  const id = process.env.REACT_APP_ID;
  
  const postUrl = `${post}?u=${u}&id=${id}`;

  return (
    <div
      className={`${opacity} d-flex justify-content-center align-items-center info-newsletter flex-column`}
    >
      <div className="newsletter-content w-100 d-flex">
        <h1 className="new-sletter-headline-f">NEWSLETTER</h1>
        <h1 className="new-sletter-headline">
          Prijavi se i prvi dobi najnovije ponude od nas!
        </h1>
        <h6 className="new-sletter-headline-sec">
          Budi u toku sa našim najnovijim dešavanjima
        </h6>
        <div className="d-flex justify-content-around align-items-center news-w">
          <div className="form">
            <MailchimpSubscribe url={postUrl} />
          </div>
        </div>
        <span className="enter-v">
          Unesite vašu email adresu da bi ste se prijavili za newsletter
        </span>
      </div>
      <div className="w-100 d-flex justify-content-center align-items-center pb-2 flex-column">
        <a href={`${to}`} className="animate-scroll">
          <i className="bi bi-chevron-down"></i>
        </a>
      </div>
    </div>
  );
}

export default Newsletter;
