import React, { useState, useLayoutEffect } from "react";
import { NavLink } from "react-router-dom";
import Logo from "../images/logo inex viljuskari.jpg";

function Header({ page }) {
  const [burgerWhite, setBurgerWhite] = useState(false);
  const [burgerRed, setBurgerRed] = useState(false);
  const [classSroll, setClassScroll] = useState(false);

  useLayoutEffect(() => {
    setBurgerWhite(false);
    setBurgerRed(false);
  }, []);

  useLayoutEffect(() => {
    document.addEventListener("scroll", (e) => {
      if (window.scrollY > 50) {
        setClassScroll(true);
      } else {
        setClassScroll(false);
      }
    });
  }, []);

  return (
    <React.Fragment>
      <header className="header d-flex justify-content-between align-items-center w-100">
        <NavLink to="/">
          <img src={Logo} alt="Inex Viljuškari logo" className="logo-header" />
        </NavLink>
        <div className="header-info  justify-content-between align-items-center">
          <div className="header-block d-flex justify-content-between align-items-start flex-column">
            <div className="d-flex justify-content-around align-items-center">
              <i className="bi-phone text-danger icon"></i>
              <span className="block-label text-muted">Pozovite nas</span>
            </div>
            <span className="bold-info">+381 060-306-760-4</span>
          </div>
          <div className="header-block d-flex justify-content-between align-items-start flex-column">
            <div className="d-flex justify-content-around align-items-center">
              <i className="bi-clock text-danger icon "></i>
              <span className="block-label text-muted">Radno vreme</span>
            </div>
            <span className="bold-info">08:00 &nbsp; - &nbsp; 18:00</span>
          </div>
          <div className="header-block d-flex justify-content-between align-items-start flex-column">
            <div className="d-flex justify-content-around align-items-center">
              <i className="bi-envelope text-danger icon"></i>
              <span className="block-label text-muted">Email</span>
            </div>
            <span className="bold-info">inexviljuskari@gmail.com</span>
          </div>
        </div>
        <i
          className="bi-list header-list"
          onClick={() => {
            setBurgerWhite(true);
          }}
        ></i>
      </header>
      <nav
        className={
          classSroll
            ? "navigaion d-flex align-items-center w-100 red"
            : "navigaion d-flex align-items-center w-100 transparent"
        }
      >
        <div className="d-flex justify-content-between align-items-center ">
          <NavLink to="/" className="link-nav">
            Naslovna
          </NavLink>
          <NavLink to="/naseusluge" className="link-nav">
            Naše usluge
          </NavLink>
          <NavLink to="/servis/servis" className="link-nav">
            Servisi
          </NavLink>
          <NavLink to="/servis/oprema" className="link-nav">
            Oprema
          </NavLink>
          <NavLink to="/servis/detektazakvara" className="link-nav">
            Detektaža kvara
          </NavLink>
          <NavLink to="/servis/rezervnidelovi" className="link-nav">
            Rezervni delovi
          </NavLink>
          {page === "home" ? (
            <React.Fragment>
              <a href="#about" className="link-nav">
                O nama
              </a>
              <a href="#contact" className="link-nav">
                Kontakt
              </a>
            </React.Fragment>
          ) : (
            <React.Fragment>
              <a href="/#about" className="link-nav">
                O nama
              </a>
              <a href="/#contact" className="link-nav">
                Kontakt
              </a>
            </React.Fragment>
          )}
        </div>
        <div className="d-flex justify-content-between align-items-center">
          <i className="bi-instagram media-icons"></i>
          <i className="bi-facebook media-icons"></i>
        </div>
        <i
          className="bi-list"
          onClick={() => {
            setBurgerRed(true);
          }}
        ></i>
      </nav>
      {
        <div className={burgerRed ? "red-burger show" : "red-burger hide"}>
          <svg
            xmlns="http://www.w3.org/2000/svg"
            onClick={() => {
              setBurgerRed(false);
            }}
            width="46"
            height="46"
            fill="#F5F5F5"
            className="mb-3 bi bi-x-lg close"
            viewBox="0 0 16 16"
          >
            <path
              fillRule="evenodd"
              d="M13.854 2.146a.5.5 0 0 1 0 .708l-11 11a.5.5 0 0 1-.708-.708l11-11a.5.5 0 0 1 .708 0Z"
            />
            <path
              fillRule="evenodd"
              d="M2.146 2.146a.5.5 0 0 0 0 .708l11 11a.5.5 0 0 0 .708-.708l-11-11a.5.5 0 0 0-.708 0Z"
            />
          </svg>
          <div className="d-flex justify-content-between align-items-center flex-column">
            <NavLink
              to="/"
              className="link-nav-burger"
              onClick={() => {
                setBurgerRed(false);
              }}
            >
              Naslovna
            </NavLink>
            <NavLink
              to="/naseusluge"
              className="link-nav-burger"
              onClick={() => {
                setBurgerRed(false);
              }}
            >
              Naše usluge
            </NavLink>
            <NavLink
              to="/servis/rezervnidelovi"
              className="link-nav-burger"
              onClick={() => {
                setBurgerRed(false);
              }}
            >
              Rezervni delovi
            </NavLink>
            <NavLink
              to="/servis/oprema"
              className="link-nav-burger"
              onClick={() => {
                setBurgerRed(false);
              }}
            >
              Oprema
            </NavLink>
            <NavLink
              to="/servis/detektazakvara"
              className="link-nav-burger"
              onClick={() => {
                setBurgerRed(false);
              }}
            >
              Detektaža kvara
            </NavLink>
            <NavLink
              to="/servis/servis"
              className="link-nav-burger"
              onClick={() => {
                setBurgerRed(false);
              }}
            >
              Servis
            </NavLink>
            {page === "home" ? (
              <React.Fragment>
                <a
                  href="#about"
                  className="link-nav-burger"
                  onClick={() => {
                    setBurgerRed(false);
                  }}
                >
                  O nama
                </a>
                <a
                  href="#contact"
                  className="link-nav-burger"
                  onClick={() => {
                    setBurgerRed(false);
                  }}
                >
                  Kontakt
                </a>
              </React.Fragment>
            ) : (
              <React.Fragment>
                <a
                  href="/#about"
                  className="link-nav-burger"
                  onClick={() => {
                    setBurgerRed(false);
                  }}
                >
                  O nama
                </a>
                <a
                  href="/#contact"
                  className="link-nav-burger"
                  onClick={() => {
                    setBurgerRed(false);
                  }}
                >
                  Kontakt
                </a>
              </React.Fragment>
            )}
            <i className="bi-instagram link-nav-burger"></i>
            <i className="bi-facebook link-nav-burger"></i>
          </div>
        </div>
      }

      {
        <div
          className={burgerWhite ? "white-burger show" : "white-burger hide"}
        >
          <svg
            xmlns="http://www.w3.org/2000/svg"
            onClick={() => {
              setBurgerWhite(false);
            }}
            width="46"
            height="46"
            fill="#FF0000"
            className="mb-3 bi bi-x-lg close"
            viewBox="0 0 16 16"
          >
            <path
              fillRule="evenodd"
              d="M13.854 2.146a.5.5 0 0 1 0 .708l-11 11a.5.5 0 0 1-.708-.708l11-11a.5.5 0 0 1 .708 0Z"
            />
            <path
              fillRule="evenodd"
              d="M2.146 2.146a.5.5 0 0 0 0 .708l11 11a.5.5 0 0 0 .708-.708l-11-11a.5.5 0 0 0-.708 0Z"
            />
          </svg>

          <div className="d-flex justify-content-between align-items-start flex-column mt-5">
            <div className="header-block d-flex justify-content-between align-items-start flex-column">
              <div className="d-flex justify-content-around align-items-center">
                <i className="bi-phone text-danger icon"></i>
                <span className="block-label text-muted">Pozovite nas</span>
              </div>
              <span className="bold-info">+381 060-306-760-4</span>
            </div>
            <div className="header-block d-flex justify-content-between align-items-start flex-column mt-5">
              <div className="d-flex justify-content-around align-items-center">
                <i className="bi-clock text-danger icon "></i>
                <span className="block-label text-muted">Radno vreme</span>
              </div>
              <span className="bold-info">08:00 &nbsp; - &nbsp; 18:00</span>
            </div>
            <div className="header-block d-flex justify-content-between align-items-start flex-column mt-5">
              <div className="d-flex justify-content-around align-items-center">
                <i className="bi-envelope text-danger icon"></i>
                <span className="block-label text-muted">Email</span>
              </div>
              <span className="bold-info">inexviljuskari@gmail.com</span>
            </div>
          </div>
        </div>
      }
    </React.Fragment>
  );
}

export default Header;
