import React from "react";
import { NavLink } from "react-router-dom";

function Blocks() {
  setTimeout(() => {
    const left = document.querySelectorAll(".block-info");

    const observer = new IntersectionObserver(
      (entries) => {
        entries.forEach((entry) => {
          entry.target.classList.toggle("show-block", entry.isIntersecting);
        });
      },
      {
        threshold: 0.6,
      }
    );

    left.forEach((card) => {
      observer.observe(card);
    });
  }, 200);

  return (
    <div className="w-100 d-flex justify-content-center align-items-center flex-column mt-5 mb-5">
      <h1 className="block-headline services-headline">NAŠE USLUGE</h1>
      <div className="lineone"></div>
      <div className="linetwo"></div>
      <div className="short-info justify-content-between align-items-start mt-4">
        <div className="justify-content-center align-items-start flex-column block-info">
          <div className="block-banner-div block-0"></div>
          <div className="wrp-txt">
            <h1 className="block-headline">SERVIS</h1>
            <span className="text-muted desc ">
              Iskusni i obučeni serviseri vrše popravku i servis viljuškara kako
              u servisu tako i na terenu kod klijenta.
            </span>
            <br />
            <NavLink
              to="/servis/servis"
              className="more"
              style={{ colo: "red", textDecoration: "none" }}
            >
              VIŠE &nbsp; ›
            </NavLink>
          </div>{" "}
        </div>
        <div className="justify-content-center align-items-start flex-column block-info">
          <div className="block-banner-div block-1"></div>
          <div className="wrp-txt">
            <h1 className="block-headline">DETEKTAŽA KVARA</h1>
            <span className="text-muted desc">
              Pružamo mogućnost detaljnog pregleda i defektaže kvara na mašini.
              Na osnovu toga možemo dati preciznu ponudu za popravku.
            </span>
            <br />
            <NavLink
              to="/servis/detektazakvara"
              className="more"
              style={{ colo: "red", textDecoration: "none" }}
            >
              VIŠE &nbsp; ›
            </NavLink>
          </div>
        </div>
        <div className="justify-content-center align-items-start flex-column block-info">
          <div className="block-banner-div block-2"></div>
          <div className="wrp-txt">
            <h1 className="block-headline">REZERVNI DELOVI</h1>
            <span className="text-muted desc">
              Pošalji nam online upit putem mail-a.
            </span>
            <br />
            <a
              href="#contact"
              className="more"
              style={{ colo: "red", textDecoration: "none" }}
            >
              POŠALJI &nbsp; ›
            </a>
          </div>{" "}
        </div>
        <div className="justify-content-center align-items-start flex-column block-info">
          <div className="block-banner-div block-3"></div>
          <div className="wrp-txt">
            <h1 className="block-headline">OPREMA</h1>
            <span className="text-muted desc">
              Pošalji nam online upit putem mail-a.
            </span>
            <br />
            <a
              href="#contact"
              className="more"
              style={{ colo: "red", textDecoration: "none" }}
            >
              POŠALJI &nbsp; ›
            </a>
          </div>{" "}
        </div>
      </div>
    </div>
  );
}

export default Blocks;
