/* eslint-disable jsx-a11y/no-distracting-elements */
import React from "react";
import ImageOne from "../images/tcm 3.png";
import ImageTwo from "../images/hangsha-logo.png";
import ImageThree from "../images/yanmar-vector-logo.png";
import ImageFour from "../images/cat-logo.jpg";
import ImageFive from "../images/Case_Construction_logo.svg.png";
import Linde from "../images/linde.png";
import Jung from "../images/Jungheinrich_AG_Logo.png";
import Toyota from "../images/Toyota.png";
import Nissan from "../images/nissan.png";
import Hyundai from "../images/hyundai.jpg";
import Doosan from "../images/doosan.png";
import Clark from "../images/clark.jpg";
import Mitshubishi from "../images/mitshubishi.png";
import Perkins from "../images/Perkins-Logo.svg.png";
import Still from "../images/still.png";
import Yale from "../images/Yale_Materials_Handling_Corporation_(logo).png";
import Hister from "../images/hyster-logo-0D1819B9A0-seeklogo.com.png";
import Komatsu from "../images/Komatsu_logo.png";
import Heli from "../images/HELI.png";

function Partners() {
  return (
    <div
      className="w-100 d-flex justify-content-center align-items-center partners-wrp"
      id="partners"
    >
      <div className="partners d-flex justify-content-center align-items-center flex-column ">
        <h1 className="block-headline partner-headline">BRENDOVI</h1>
        <div className="lineone"></div>
        <div className="linetwo"></div>
        <div className="images-partner justify-content-between align-items-start mt-2">
          <marquee behavior="scroll" direction="left" scrollamount={13}>
            <img src={ImageOne} alt="Logo TCM" className="partner-logo" />
            <img src={ImageTwo} alt="Logo Hangsha" className="partner-logo" />
            <img src={ImageThree} alt="Logo Yanmar" className="partner-logo" />
            <img src={ImageFour} alt="Logo CAT" className="partner-logo" />
            <img
              src={ImageFive}
              alt="Logo Case Construction"
              className="partner-logo"
            />
            <img src={Linde} alt="Logo Linde" className="partner-logo" />
            <img
              src={Jung}
              alt="Logo Jungheinrich AG"
              className="partner-logo"
            />
            <img src={Toyota} alt="Logo Toyota" className="partner-logo" />
            <img src={Nissan} alt="Logo Nissan" className="partner-logo" />
            <img src={Hyundai} alt="Logo Hyundai" className="partner-logo" />
            <img src={Doosan} alt="Logo Doosan" className="partner-logo" />
            <img src={Clark} alt="Logo Clark" className="partner-logo" />
            <img src={Mitshubishi} alt="Logo " className="partner-logo" />
            <img
              src={Perkins}
              alt="Logo Mitshubishi"
              className="partner-logo"
            />
            <img src={Still} alt="Logo Perkins" className="partner-logo" />
            <img src={Yale} alt="Logo Still" className="partner-logo" />
            <img
              src={Hister}
              alt="Logo Yale Materials Handling Corporation"
              className="partner-logo"
            />
            <img src={Komatsu} alt="Logo Komatsu" className="partner-logo" />
            <img src={Heli} alt="Logo HELI" className="partner-logo" />
          </marquee>
        </div>
      </div>
    </div>
  );
}

export default Partners;
