import React from "react";
import ImageOne from "../images/block0.jpg";
import ImageTwo from "../images/block1.jpg";
import ImageThree from "../images/block2.jpg";
import ImageFour from "../images/block3.jpg";

function ServicesDetails() {
  setTimeout(() => {
    const left = document.querySelectorAll(".img-left");
    const right = document.querySelectorAll(".img-right");
    const leftTxt = document.querySelectorAll(".txt-left");
    const rightTxt = document.querySelectorAll(".txt-right");

    const observerLeft = new IntersectionObserver(
      (entries) => {
        entries.forEach((entry) => {
          entry.target.classList.toggle("show-left", entry.isIntersecting);
        });
      },
      {
        threshold: 0,
      }
    );

    const observerRight = new IntersectionObserver(
      (entries) => {
        entries.forEach((entry) => {
          entry.target.classList.toggle("show-right", entry.isIntersecting);
        });
      },
      {
        threshold: 0,
      }
    );

    const observerLeftText = new IntersectionObserver(
      (entries) => {
        entries.forEach((entry) => {
          entry.target.classList.toggle("show-left-txt", entry.isIntersecting);
        });
      },
      {
        threshold: 0,
      }
    );

    const observerRightText = new IntersectionObserver(
      (entries) => {
        entries.forEach((entry) => {
          entry.target.classList.toggle("show-right-txt", entry.isIntersecting);
        });
      },
      {
        threshold: 0,
      }
    );

    left.forEach((card) => {
      observerLeft.observe(card);
    });

    right.forEach((card) => {
      observerRight.observe(card);
    });

    leftTxt.forEach((card) => {
      observerLeftText.observe(card);
    });

    rightTxt.forEach((card) => {
      observerRightText.observe(card);
    });
  }, 200);

  return (
    <div>
      <div className="w-100 d-flex justify-content-center align-items-center flex-column mt-5">
        <div
          className="w-100 d-flex justify-content-start align-items-center flex-column"
          id="servis"
        >
          <h1 className="service-headline">SERVIS</h1>
          <div className="lineone"></div>
          <div className="linetwo"></div>
          <div className="w-100 d-flex justify-content-start align-items-center description-services">
            <img
              src={ImageOne}
              alt="Slika za servis"
              className="img-inf img-left"
            />
            <span className="sp-inf txt-right text-border">
              Iskusni i obučeni serviseri vrše popravku i servis viljuškara kako
              u servisu tako i na terenu kod klijenta.
            </span>
          </div>
        </div>

        <div
          className="w-100 d-flex justify-content-end align-items-center flex-column mt-5"
          id="detektazakvara"
        >
          <h1 className="service-headline">DETEKTAŽA KVARA</h1>
          <div className="lineone"></div>
          <div className="linetwo"></div>
          <div className="w-100 d-flex justify-content-end align-items-center description-services">
            <span className="sp-inf txt-left text-border">
              {" "}
              Pružamo mogućnost detaljnog pregleda i defektaže kvara na
              mašini.Na osnovu toga možemo dati preciznu ponudu za popravku.
            </span>
            <img
              src={ImageTwo}
              alt="Slika za detektažu kvara"
              className="img-inf img-right"
            />
          </div>
        </div>

        <div
          className="w-100 d-flex justify-content-center align-items-center flex-column mt-5"
          id="rezervnidelovi"
        >
          <h1 className="service-headline">REZERVNI DELOVI</h1>
          <div className="lineone"></div>
          <div className="linetwo"></div>
          <div className="w-100 d-flex justify-content-start align-items-center description-services">
            <img
              src={ImageThree}
              alt="Slika za rezervne delove"
              className="img-inf img-left"
            />
            <div className="fix-txt txt-right">
              <span className="sp-inf text-border">
                Pošalji nam online upit putem mail-a.
              </span>
              <br />
              <br />
              <a href="/#contact" className="sendMail">
                POŠALJI &nbsp; ›
              </a>
            </div>
          </div>
        </div>

        <div
          className="w-100 d-flex justify-content-center align-items-center flex-column mt-5"
          id="oprema"
        >
          <h1 className="service-headline">OPREMA</h1>
          <div className="lineone"></div>
          <div className="linetwo"></div>
          <div className="w-100 d-flex justify-content-end align-items-center description-services">
            <div className="fix-txt txt-left">
              <span className="sp-inf text-border">
                Pošalji nam online upit putem mail-a.
              </span>
              <br />
              <br />
              <a href="/#contact" className="sendMail">
                POŠALJI &nbsp; ›
              </a>
            </div>
            <img
              src={ImageFour}
              alt="Slika za opremu"
              className="img-inf img-right"
            />
            <br />
          </div>
        </div>
      </div>
    </div>
  );
}

export default ServicesDetails;
