import React from "react";
import { NavLink } from "react-router-dom";

function AboutUs() {
  return (
    <React.Fragment>
      <div
        className="d-flex justify-content-center align-items-center flex-column info-about-us"
        id="about"
      >
        <div className="d-flex justify-content-center align-items-center flex-column info-about-us-hover">
          <h1 className="about-us">VIŠE O NAMA</h1>
          <span className="red-txt">upoznajte inex D.O.O.</span>
          <div className="border-div-btm"></div>
          <span className="text">
            Preduzeće Inex Viljuškari sa sedištem u Beogradu posluje od aprila
            2018 godine. Osnovna delatnost preduzeća je uvoz i prodaja rezervnih
            delova za viljuškare i gradjevinske mašine,profesionalne skladišne
            opreme i tehnike, ptoizvedene u Aziji i Evropi,poznatih svetskih
            proizvodjača. Posedovanje obimne tehničke dokumentacije za LINDE,
            JUNGHEINRICH, CATERPILLAR, NISSAN, HANGSHA, OSAKA, TCM, TOYOTA,
            HYSTER, YALE, HELI...i mnoge druge tipove viljuškara kao i
            partnerstvo sa vodećom svetskom firmom iz te oblasti omogućava nam
            kvalitetnu i brzu uslugu prema našim
          </span>
          <span className="text">klijentima.</span>
          <span className="text">
            Pored rezervnih delova možemo vam ponuditi i servis na celoj
            teritoriji Srbije. Servisiranje podrazumeva kompletan posao, od
            nabavke delova do ugradnje istih. Servisiranje obavljamo na
            terenu,kada je moguće. Komlikovsnije remonte (100) obavljamo u
            servisu. Naši serviseri ispešno rešavaju probleme na svim sklopovima
            viljuškara: trap, upravljački mehanizam, elektronika, transmisija,
            hidraulika, motorna grupa, gasni uredjaji. Opšte prihvaćeno pravilo
            je da se novac uložen u pravilno održavanje opreme višestruko vraća.
            Pored rezervnih delova i servisa možemo vam ponuditi i gume za
            viljuškare svih dimenzija u više nivoa kvaliteta.
          </span>
          <NavLink
            to="/naseusluge"
            className="more override"
            style={{ colo: "red", textDecoration: "none" }}
          >
            NAŠE USLUGE &nbsp; ›
          </NavLink>
        </div>
      </div>
    </React.Fragment>
  );
}

export default AboutUs;
