import React from "react";
import Logo from "../images/logo inex viljuskari.jpg";
import { NavLink } from "react-router-dom";
import FB from "../images/fblogo.png";
import IG from "../images/iglogo.png";

function Sitemap({ page }) {
  return (
    <div className="sitemap w-100 d-flex">
      <div className="d-flex blck-1 flex-column mt-3">
        <img
          src={Logo}
          alt="Inex Viljuškari logo"
          className="partner-logo logo-map"
        />
        <span className="wht">
          Preduzeće Inex Viljuškari sa sedištem u Beogradu{" "}
        </span>
        <span className="wht" style={{ marginTop: "-5px" }}>
          posluje od 2018 godine.
        </span>
        <div className="d-flex w-100 justify-content-start align-items-start">
          <img src={FB} alt="Logo Facebook" className="soc-med-logo" />
          <img src={IG} alt="Logo Instagram" className="soc-med-logo" />
        </div>
      </div>

      <div className="d-flex justify-content-center align-items-start flex-column mt-3">
        <h5 className="mp mp-border">MAPA SAJTA</h5>
        <NavLink to="/" className="l-color">
          Naslovna
        </NavLink>
        {page === "home" ? (
          <React.Fragment>
            <a href="#about" className="l-color">
              O nama
            </a>
            <a href="#contact" className="l-color">
              Kontakt
            </a>
            <a href="#partners" className="l-color">
              Partneri
            </a>
          </React.Fragment>
        ) : (
          <React.Fragment>
            <NavLink to="/" className="l-color">
              O nama
            </NavLink>
            <NavLink to="/" className="l-color">
              Kontakt
            </NavLink>
            <NavLink to="/" className="l-color">
              Brendovi
            </NavLink>
          </React.Fragment>
        )}
        <NavLink to="/naseusluge" className="l-color">
          Naše usluge
        </NavLink>
      </div>

      <div className="d-flex justify-content-center align-items-start flex-column mt-3">
        <h5 className="mp mp-border">NAŠE USLUGE</h5>
        <NavLink to="/naseusluge" className="l-color">
          Naše usluge
        </NavLink>
        <NavLink to="/servis/rezervnidelovi" className="l-color">
          Rezervni delovi
        </NavLink>
        <NavLink to="/servis/detektazakvara" className="l-color">
          Detektaža kvara
        </NavLink>
        <NavLink to="/servis/oprema" className="l-color">
          Oprema
        </NavLink>
        <NavLink to="/servis/servis" className="l-color">
          Servis
        </NavLink>
      </div>

      <div className="d-flex justify-content-center align-items-start flex-column mt-3 brands-map">
        <h5 className="mp mp-border">BRENDOVI</h5>
        <span className="l-color">TCM</span>
        <span className="l-color">HANGCHA</span>
        <span className="l-color">YANMAR</span>
        <span className="l-color">CAT</span>
        <span className="l-color">CASE CONSTRUCTION</span>
        <span className="l-color">LINDE</span>
        <span className="l-color">YUNGHINRICH</span>
        <span className="l-color">TOYOTA</span>
        <span className="l-color">NISSAN</span>
        <span className="l-color">HYUNDAI</span>
        <span className="l-color">DOSAAN</span>
        <span className="l-color">CLARK</span>
        <span className="l-color">MITSUBISHI</span>
        <span className="l-color">PERKINS</span>
        <span className="l-color">STILL</span>
        <span className="l-color">YALE</span>
        <span className="l-color">HAYSTER</span>
        <span className="l-color">KOMATSU</span>
        <span className="l-color">HELI</span>
      </div>
      <div className="d-flex justify-content-center align-items-start flex-column mt-3">
        <h5 className="mp mp-border">KAKO DO NAS</h5>
        <h6 className="mp">LOKACIJA</h6>
        <span className="l-color top-mar">
          Doktora Subotica Starijeg 13, 11000 Beograd.
        </span>
        <h6 className="mp">PIB</h6>
        <span className="l-color top-mar">110627536</span>
        <h6 className="mp">MATIČNI BROJ</h6>
        <span className="l-color top-mar">21369837</span>
        <h6 className="mp">TELEFON</h6>
        <span className="l-color top-mar">+381 060-306-760-4</span>
        <h6 className="mp">MAIL</h6>
        <span className="l-color top-mar">inexviljuskari@gmail.com</span>
        <h6 className="mp">RADNO VREME</h6>
        <span className="l-color top-mar">08:00 - 18:00</span>
      </div>
    </div>
  );
}

export default Sitemap;
