import React, { useRef, useState } from "react";
import emailjs from "emailjs-com";

function ContactUs() {
  const form = useRef();
  const [message, setMessage] = useState("");
  const [messageClass, setMessageClass] = useState("");

  const sendEmail = (e) => {
    e.preventDefault();

    emailjs
      .sendForm(
        process.env.REACT_APP_SERVICE_ID,
        process.env.REACT_APP_TEMPLATE,
        form.current,
        process.env.REACT_APP_USER_ID
      )
      .then(
        (result) => {
          if (result.text === "OK") {
            setMessage("Poslato!");
          } else {
            setMessage(result.text);
          }
          setMessageClass("text-success");
          e.target.reset();
        },
        (error) => {
          setMessage(error.text);
          setMessageClass("text-danger");
        }
      );
  };

  return (
    <form
      className="contact-form w-100 d-flex justify-content-center align-items-center"
      ref={form}
      onSubmit={sendEmail}
      id="contact"
    >
      <div className="fontact-form-dialog d-flex justify-content-center align-items-center flex-column">
        <h1 className="services-headline block-headline">Kontaktiraj nas</h1>
        <div className="lineone"></div>
        <div className="linetwo"></div>
        <input
          type="email"
          name="to_name"
          id=""
          placeholder="Email"
          className="contact-input"
          required
          autoComplete="off"
        />
        <input
          type="text"
          name="from_name"
          id=""
          placeholder="Naziv"
          className="contact-input"
          required
          autoComplete="off"
        />
        <textarea
          name="message"
          id=""
          cols="30"
          rows="10"
          className="contact-input"
          placeholder="Poruka"
          autoComplete="off"
          required
        ></textarea>
        <input type="submit" value="Pošalji" className="send-msg" />
        <span className={messageClass}>{message}</span>
      </div>
    </form>
  );
}

export default ContactUs;
